.products {
    width: 100%;
    padding-top: 121px;
    padding-bottom: 98px;
    @include tablet {
        padding-top: 122px;
        padding-bottom: 112px;
    }
    @include desktop {
        padding-top: 127px;
        padding-bottom: 119px;
    }
    
    
    .products__pretitle {
    margin-bottom: 15px;
    }
    
    .products__title {
        margin-bottom: 144px;
    }
    
    .products__list {
    @include centerFlex ($value: flex, $alitem: center, $justcont: center);
    flex-direction: column;
    @include tablet {
    align-items: start;
    flex-direction: row;
        }

}
    
.products__list-item {
    @include centerFlex ($value: flex, $alitem: center, $justcont: null);
    flex-direction: column;
    position: relative;
    max-width: 280px;
    border-radius: 24px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
    padding-top: 161px;
    &:not(:last-child) {
    margin-bottom: 130px;
    }
    
    @include tablet {
    margin-bottom: 0;
    max-width: 220px;
    padding-left: 11px;
    padding-right: 11px;
    &:not(:last-child) {
        margin-bottom: 0;
    margin-right: 20px;}
    }
    @include desktop {
    max-width: 329px;
    padding-left: 44px;
    padding-right: 44px;
    &:not(:last-child) {
    margin-right: 30px;
                    }
            }
    &::before{
        content:"";
        position: absolute;
        top: 0; left: 50%;
        transform: translate(-50%, -50%);
        background-size: contain;
        background-repeat: no-repeat;
        width: 100%;
        height: 252px;
        background-position:center;
        
    }
    @for $i from 1 through 3 {
        &:nth-child(#{$i}){
background-color: var(--decor-color-#{$i});
            &::before{
                background-image: url(../images/products/products-mob-#{$i}@1x.png);
                @include appleimg {
                    background-image: url(../images/products/products-mob-#{$i}@2x.png);
                }
                @include tablet {
                    background-image: url(../images/products/products-tab-#{$i}@1x.png);
                    @include appleimg {
                        background-image: url(../images/products/products-tab-#{$i}@2x.png);
                    }
                }
                @include desktop {
                    background-image: url(../images/products/products-desk-#{$i}@1x.png);
                    @include appleimg {
                        background-image: url(../images/products/products-desk-#{$i}@2x.png);
                    }
                }
            }
        }
    }
}
.products__list-name {
    @include font ($fs:20px, $fw:null, $lh:1.15, $ls:0.04em);
    font-family:var(--text-secondary);
    color:var(--text-white-color);
    font-style: normal;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 30px;
    
    @include desktop {
    @include font ($fs:34px, $fw:null, $lh:1.13, $ls:0.04em);
    }
}
.products__list-name::after{
    content:url(../images/products/icon-ellipsis.svg);
    color:var(--text-white-color);
    display: flex;
    justify-content: center;
    margin-top: 30px;
}
.products__list-text {
    @include font ($fs:14px, $fw:700, $lh:1.52, $ls:0.04em);
    color:var(--text-white-color);
    font-family: "DM Sans";
    text-align: center;
    margin-bottom: 30px;
    @include tablet{
    line-height: 1.76;}
    @include desktop{
        font-size: 16px;
}
}

.products__btn {
    color: #D41443;
    border-radius: 50px;
    width: 40px;
    height: 40px;
    transform: rotate(0);
    transition: 2s;
    }

    .products__btn-translate {
        transform: rotate(-90deg);
        transition: 1s;
    }


    .icon-arrow {
        fill: #D41443;
    }
}